.softContainer{
    width: 70%;
    justify-content: center;
    text-align: center;
    padding: 40px;
    margin: auto;
 }
 
 .softSkills{
   color : white;
   font-size : 1rem;
   background-color: #7040c2;
   border-radius: 1rem;
   padding: 30px;
 }

 .softSkills:hover{
    opacity: 0.2;
 }

 .softTitle{
   font-family: "Mulish", sans-serif;
   font-weight: bold;
   border-bottom : 1px;
   text-decoration: underline ;
   text-decoration-color: #7040c2;
   text-underline-offset: 5px;
   text-align: center;
 }
